.box{
    /* border: 2px solid black; */
    width:30%;
    height:100%;
    background-image:url("https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRtNzjujcX3qiXBNyL7fFdmzRCG6hEc7eZkmw&usqp=CAU");
    background-size: 100% 100%;
  }
  .main{
    
      display: flex;
    justify-content: center;
    align-content: center;

  }
 input{
     /* border: 2px solid black; */
     /* border-radius: 8px; */
     border-radius: 15px 50px;
     outline: none;
     
     text-align: center;
    /* width: 30%; */
    
    height: 30px;
    word-wrap: inherit;
    box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
-webkit-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
-moz-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);

 }
 
 input[type="search"] {
    display: block;
    margin : 0 auto;
}
 p{
     display: flex;
     justify-content: center;
     margin-top: 20%;
     background-color: red;
     

 }
 .info{
     text-align: center;
     color: white;
     overflow-wrap: break-word;

     
 }
 .Location{
     color: black;
     overflow-wrap: break-word;
     

 }
 .temp{
    text-shadow: 4px 4px 0px rgba(40,29,29,0.6);
    font-size: 50px;
    overflow-wrap: break-word;
     
 }
 @media only screen and (max-width: 930px){
	input{
width: 100%;
    }
    .box{
        height:50%;
    }

}